___CSS_LOADER_EXPORT___.push([module.id, ".calendar[data-v-4300c0c8]{max-width:770px}.calendar--navigation[data-v-4300c0c8]{display:flex;justify-content:space-between}@media screen and (max-width:600px){.calendar--navigation .label[data-v-4300c0c8]{display:none}}.days-wrapper[data-v-4300c0c8]{margin-top:30px}.callendar--loader[data-v-4300c0c8]{padding:100px 0;text-align:center}.calendar--daysname div[data-v-4300c0c8]{background-color:#212529;border:1px solid #212529;color:#fff;text-align:center}.calendar--day[data-v-4300c0c8],.calendar--daysname div[data-v-4300c0c8]{box-sizing:border-box;display:inline-block;margin-bottom:10px;margin-right:10px;padding:5px 10px;width:100px}.calendar--day[data-v-4300c0c8]{border:1px solid #000;border-radius:3px;cursor:pointer;height:100px;vertical-align:middle}.calendar--day.hidden[data-v-4300c0c8]{visibility:hidden}.calendar--day.currentDay[data-v-4300c0c8]{background:#e1e1e1}@media screen and (max-width:810px){.calendar--day[data-v-4300c0c8],.calendar--daysname div[data-v-4300c0c8]{width:calc(14.28571% - 10px)}}", "",{"version":3,"sources":["webpack://./resources/js/components/calendar/Calendar.vue"],"names":[],"mappings":"AA4KI,2BACI,eACJ,CAEA,uCACI,YAAa,CACb,6BACJ,CAEA,oCACI,8CACI,YACJ,CACJ,CAEA,+BACI,eACJ,CAEA,oCAEI,eAAgB,CADhB,iBAEJ,CAEA,yCAQI,wBAAyB,CACzB,wBAAwB,CAFxB,UAAW,CAGX,iBACJ,CAEA,yEARI,qBAAsB,CAJtB,oBAAqB,CAGrB,kBAAmB,CADnB,iBAAkB,CAGlB,gBAAiB,CAJjB,WAuBJ,CAZA,gCAEI,qBAAsB,CACtB,iBAAkB,CAOlB,cAAe,CALf,YAAa,CAMb,qBACJ,CAEA,uCACI,iBACJ,CAEA,2CACI,kBACJ,CAEA,oCAMI,yEACI,4BACJ,CACJ","sourcesContent":["<template>\n    <div class=\"calendar\">\n\n        <div class=\"calendar--navigation\">\n            <div>\n                <button class=\"btn btn-dark\" v-on:click=\"minusMonth\"><i class=\"bi bi-arrow-left\"></i> <span class=\"label\">предыдущий месяц</span></button>\n            </div>\n            <div v-bind:title=\"currentYear + '-' + currentMonth\">\n                Текущая дата: <b>{{ currentMonthFormated }} {{ currentYear }}</b>\n            </div>\n            <div>\n                <button class=\"btn btn-dark\" @click=\"plusMonth\"><span class=\"label\">следующий месяц</span> <i class=\"bi bi-arrow-right\"></i></button>\n            </div>\n        </div>\n\n        <div class=\"days-wrapper\">\n            <div class=\"calendar--daysname\">\n                <div>ПН</div>\n                <div>ВТ</div>\n                <div>СР</div>\n                <div>ЧТ</div>\n                <div>ПТ</div>\n                <div>СБ</div>\n                <div>ВС</div>\n            </div>\n            <div v-if=\"!isCalendarLoading\">\n                <Day\n                    v-for=\"day in fakeDates\"\n                    :day=\"day\"\n                    :key=\"day.id\"\n                    class=\"calendar--day hidden\"\n                ></Day>\n                <Day\n                    v-for=\"day in dates\"\n                    :day=\"day\"\n                    :key=\"day.id\"\n                    @showDayModal=\"openModal\"\n                    class=\"calendar--day\"\n                ></Day>\n            </div>\n            <div v-else class=\"callendar--loader\">Идёт загрузка</div>\n        </div>\n\n\n        <my-dialog v-model:show=\"dialogVisible\" class=\"-large\">\n            <day-view\n                v-bind:year=\"currentYear\"\n                v-bind:month=\"currentMonth\"\n                v-bind:day=\"currentDay\"\n                @add-new-training=\"addNewTraining\"\n                @remove-training=\"removeTraining\"\n            >\n            </day-view>\n        </my-dialog>\n\n<!--        <my-modal v-model:show=\"dialogVisible\" id=\"day-modal\">-->\n<!--            222222-->\n<!--        </my-modal>-->\n    </div>\n</template>\n\n<script>\nimport axios from \"axios\";\nimport Day from \"./Day\";\nimport DayView from \"./View\";\n\nexport default {\n    name: \"Calendar\",\n    components: {DayView, Day},\n    data() {\n        return {\n            // массив фейковых дней для заполнения календаря, если месяц начинается не с понедельника\n            fakeDates: [],\n            dates: [],\n            // данные с запланнированными/проведёнными на выбранный период днями с тренировками\n            activities: [],\n            isCalendarLoading: true,\n            dialogVisible: false,\n            currentMonth: new Date().getMonth() + 1,\n            currentYear: new Date().getFullYear(),\n            currentDay: null,\n            // myModal: new bootstrap.Modal(document.getElementById('#day-modal'), options),\n        }\n    },\n    methods: {\n        async fetchCalendarDays() {\n            try {\n                this.isCalendarLoading = true;\n\n                const response = await axios.get(this.$store.state.api_url + '/days/' + this.currentYear + '/' + this.currentMonth, {});\n                this.activities = response.data;\n\n                this.dates = [];\n                this.fakeDates = [];\n\n                for (let i = 0; i < (new Date(this.currentYear, this.currentMonth - 1, 0).getDay()); i++) {\n                    this.fakeDates.push({id: i, day: i});\n                }\n\n                for (let i = 1; i <= new Date(this.currentYear, this.currentMonth, 0).getDate(); i++) {\n                    this.dates.push(\n                        {\n                            id: i,\n                            day: i,\n                            month: this.currentMonth,\n                            year: this.currentYear\n                        }\n                    );\n\n                    if (this.activities[i]) {\n                        this.dates[i - 1].trainings = this.activities[i].trainings\n                    }\n                }\n\n            } catch (e) {\n                console.log(e);\n            } finally {\n                this.isCalendarLoading = false;\n            }\n        },\n        minusMonth() {\n            this.currentMonth--;\n\n            if (this.currentMonth < 1) {\n                this.currentMonth = 12;\n                this.currentYear--;\n            }\n        },\n        plusMonth() {\n            this.currentMonth++;\n\n            if (this.currentMonth > 12) {\n                this.currentMonth = 1;\n                this.currentYear++;\n            }\n        },\n        openModal(day) {\n            this.dialogVisible = true;\n            this.currentDay = day;\n        },\n        closeModal() {\n            this.dialogVisible = false;\n            this.currentDay = null;\n        },\n        addNewTraining(t) {\n            if (!this.dates[t.day- 1].trainings) {\n                this.dates[t.day - 1].trainings = 0;\n            }\n            this.dates[t.day - 1].trainings++;\n        },\n        removeTraining(t) {\n            this.dates[new Date(t.start_at).getDate() - 1].trainings--;\n        }\n    },\n    mounted() {\n        this.fetchCalendarDays();\n    },\n    computed: {\n        currentMonthFormated() {\n            // return this.currentMonth < 10 ? '0' + this.currentMonth : this.currentMonth;\n            return this.$store.state.months[this.currentMonth - 1];\n        }\n    },\n    watch: {\n        currentMonth() {\n            this.fetchCalendarDays();\n        }\n    }\n}\n</script>\n\n<style scoped>\n    .calendar {\n        max-width: 770px;\n    }\n\n    .calendar--navigation {\n        display: flex;\n        justify-content: space-between;\n    }\n\n    @media screen and (max-width: 600px) {\n        .calendar--navigation .label {\n            display: none;\n        }\n    }\n\n    .days-wrapper {\n        margin-top: 30px;\n    }\n\n    .callendar--loader {\n        text-align: center;\n        padding: 100px 0;\n    }\n\n    .calendar--daysname div {\n        display: inline-block;\n        width:100px;\n        margin-right: 10px;\n        margin-bottom: 10px;\n        box-sizing: border-box;\n        padding: 5px 10px;\n        color: #fff;\n        background-color: #212529;\n        border:1px solid #212529;\n        text-align: center;\n    }\n\n    .calendar--day {\n        display: inline-block;\n        border:1px solid black;\n        border-radius: 3px;\n        width:100px;\n        height: 100px;\n        margin-right: 10px;\n        margin-bottom: 10px;\n        box-sizing: border-box;\n        padding: 5px 10px;\n        cursor: pointer;\n        vertical-align: middle;\n    }\n\n    .calendar--day.hidden {\n        visibility: hidden;\n    }\n\n    .calendar--day.currentDay {\n        background: #e1e1e1;\n    }\n\n    @media screen and (max-width: 810px) {\n        .calendar {\n        }\n        .calendar--daysname div {\n            width: calc(100% / 7 - 10px);\n        }\n        .calendar--day {\n            width: calc(100% / 7 - 10px);\n        }\n    }\n\n</style>\n"],"sourceRoot":""}]);
