       }\n    },\n    methods: {\n        saveActivitie() {\n            if (!this.aa.id) {\n                this.$emit('create', this.aa)\n            } else {\n                this.$emit('update', this.aa)\n            }\n\n            this.clearForm();\n        },\n        clearForm() {\n            this.aa = {\n                id: '',\n                name: '',\n                body_part: '',\n                description: ''\n            };\n        }\n    },\n    computed: {\n        bodyParts() {\n            let bp = [];\n            this.$store.state.body_parts.forEach(i => {\n                bp.push({'value' : i.id, 'name' : i.name});\n            })\n            return bp;\n        }\n    }\n}\n</script>\n\n<style scoped>\nform {\n    display: flex;\n    flex-direction: column;\n    width: 500px;\n}\n@media screen and (max-width: 570px) {\n    form {\n        width: 100%;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
