___CSS_LOADER_EXPORT___.push([module.id, ".input[data-v-123237e9]{display:inline;width:300px}", "",{"version":3,"sources":["webpack://./resources/js/components/UI/TextEdit.vue"],"names":[],"mappings":"AA4CA,wBACI,cAAe,CACf,WACJ","sourcesContent":["<template>\n    <template v-if=\"isEdit\">\n        <input type=\"text\"\n               class=\"form-control input me-2\"\n               v-model=\"value\">\n        <button class=\"btn btn-success me-2\" @click=\"save()\"><i class=\"bi bi-check-lg\"></i></button>\n        <button class=\"btn btn-dark\" @click=\"edit()\"><i class=\"bi bi-x-lg\"></i></button>\n    </template>\n    <template v-else>\n        {{ text }}\n        <a href=\"#\" @click=\"edit()\" style=\"margin-right: 10px;\"><i class=\"bi bi-pen\"></i></a>\n    </template>\n</template>\n\n<script>\nexport default {\n    name: \"TextEdit\",\n    data() {\n        return {\n            isEdit: false,\n            value: '',\n        }\n    },\n    props: {\n        text: [String, Number]\n    },\n    mounted() {\n        this.value = this.text\n    },\n    methods: {\n        edit() {\n            this.isEdit = !this.isEdit;\n        },\n        save() {\n            if (this.value != this.text) {\n                this.$emit('save', this.value);\n            }\n            this.isEdit = !this.isEdit;\n        }\n    }\n}\n</script>\n\n<style scoped>\n.input {\n    display: inline;\n    width:300px;\n}\n</style>\n"],"sourceRoot":""}]);
